import React, {Component} from "react";
import PropTypes from "prop-types";
import _ from "lodash";

import {Expander} from "../../../../../core/components/ui/Expander";
import OraModal from "../../../../../core/components/ui/Modal";
import {compareByProductListAndMandatoryAndTitle, Filters} from "../../../../analyzer/CartInfoUtils";
import {SaveVasesButton} from "./SaveVasesButton";
import {
    TitledFoldableProductGroup,
    TitledFoldableTvVasesProductGroup
} from "../../fix/buymore/modalFragments/ModalFragments";
import {prepareDevices} from "../../../../analyzer/DeviceUtils";
import {Warning} from "../../shared/MessageBox";
import {IfCollectionNotEmpty} from "../../fix/buymore/Utils";

export default class ConvergentVasModal extends Component  {
    static propTypes = {
        header: PropTypes.string,
        bundle: PropTypes.string,
        cartBundle: PropTypes.string,
        propositionId: PropTypes.string,
        selectedSection: PropTypes.string,
        open: PropTypes.bool
    };

    /** Lifecycle **/

    constructor(props) {
        super(props);
        const {entries, mandatories, services, productList, hidden} = props;
        const vasesWithStatuses = this.prepareVasesWithStatuses(entries, mandatories, services, hidden);
        this.state = {
            productGroups: this.getProductGroups(vasesWithStatuses, productList),
            popup: false,
            callback: null,
            event: null,
            modalContent: null
        };
        this.onSaveWrapper = this.onSaveWrapper.bind(this);
    }

    componentWillReceiveProps(nextProps){
        const {entries, mandatories, services, productList, hidden} = nextProps;
        let productGroups = this.state.productGroups;
        if (!_.isEqual(entries, this.props.entries) || !_.isEqual(productList, this.props.productList)) {
            const vasesWithStatuses = this.prepareVasesWithStatuses(entries, mandatories, services, hidden);
            productGroups = this.getProductGroups(vasesWithStatuses, productList);
        }
        this.setState({
            productGroups
        });
    }

    /** Helpers **/

    markVasesAsSelected(entries, vasesWithStatuses) {
        _.forEach(entries, (vases, key) => {
            _.forEach(vases, vas => {
                vas.selected = vasesWithStatuses[key][vas.id];
            });
        })
    }

    markVasesAsMandatory(vasesWithStatuses, entries, mandatories) {

        _.forEach(mandatories, (propositionMandatories, propositionId) => {
            propositionMandatories
                .filter(productCode => entries[propositionId] && entries[propositionId][productCode])
                .map(productCode => vasesWithStatuses[propositionId].find(vas => vas.id === productCode))
                .filter(vas => vas)
                .forEach(vas => vas.mandatory = true);
        });
    }

    markVasesAsHidden(vasesWithStatuses, hidden) {
        _.forEach(vasesWithStatuses, vases => {
            _.forEach(vases, vas => {
                vas.presentation = _.includes(hidden, vas.id) ? "hidden" : "visible";
            });
        });
    }

    getProductGroups(vasesWithStatuses, productList) {
        return (productList && productList.innerLists || [])
            .map(section => {
                const type = section.productFilter;
                let products = [];
                type && _.forEach(vasesWithStatuses, (propositionVases, propositionId) => {
                    const filteredProducts = Filters()[type](propositionVases) || [];

                    products.push(
                        ...filteredProducts
                        .map(product => ({...product, propositionId}))
                    );
                });
                const enhancedProducts = [
                    ...products.filter(p => (!!p.addonType || type === 'VOICE_VASES') && !p.mandatory),
                    ...prepareDevices(products.filter(p => !!p.deviceType), this.props.descriptions, this.props.migratedProducts)];
                if (type !== "VOICE_VASES") {
                    const productsOrder = section.productCodes ? section.productCodes : [];
                    enhancedProducts.sort(compareByProductListAndMandatoryAndTitle(productsOrder));
                }
                return ({section, products: enhancedProducts});
            })
            .filter(result => result.products && result.products.length > 0);
    }

    prepareVasesWithStatuses(entries, mandatories, services, hidden) {
        let vasesWithStatuses = {};
        _.forEach(services, (proposition, propositionId) => {
            vasesWithStatuses[propositionId] = proposition.map(vas => ({
                ...vas,
                selected: false,
                mandatory: false,
                presentation: "visible"
            }));
        });
        if (Object.keys(vasesWithStatuses).length === 0) {
            return null;
        }

        this.markVasesAsSelected(vasesWithStatuses, entries);
        this.markVasesAsMandatory(vasesWithStatuses, entries, mandatories);
        this.markVasesAsHidden(vasesWithStatuses, hidden);
        return vasesWithStatuses;
    }

    /** Callbacks **/

    onSaveWrapper(){
        this.props.onClose();
        this.props.onSave();
    }

    _onClickInterceptor(productCode, propositionId, modalContent) {
        return (event) => {
            this.setState({
                popup: true,
                callback: this.props.onClick(productCode, propositionId),
                event: event,
                modalContent: modalContent
            });
        }
    }

    _runInterceptedClick() {
        this.state.callback(this.state.event);
        this._cancelInterceptedClick();
    }

    _cancelInterceptedClick() {
        this.setState({
            popup: false,
            callback: null,
            event: null,
            modalContent: null
        });
    }

    /** Render **/


    render(){

        function renderSection(command){
            if (command.type  === "TV_VASES"){
                return renderTvVasesSection(command);
            }
            return renderCommonSection(command);
        }

        function renderTvVasesSection(command){
            return (
                    <IfCollectionNotEmpty array={command.products} key={"ICNE_" + command.index}>
                        <TitledFoldableTvVasesProductGroup
                            key={"TPG_" + command.index}
                            uid={"TPG_" + command.index}
                            idx={command.type}
                            section={command.section}
                            products={command.products}
                            onClick={command.onClick}
                            onClickWrapped={command.onClickWrapped}
                            isExpanded={command.isExpanded}
                            net={command.showNetPrices}
                            configurables={command.configurables}
                            descriptions={command.descriptions}
                        />
                    </IfCollectionNotEmpty>
            );
        }

        function renderCommonSection( command) {
            return (
                <IfCollectionNotEmpty array={command.products} key={"ICNE_" + command.index}>
                    <TitledFoldableProductGroup
                        key={"TPG_" + command.index}
                        uid={"TPG_" + command.index}
                        idx={command.type}
                        section={command.section}
                        products={command.products}
                        onClick={command.onClick}
                        onClickWrapped={command.onClickWrapped}
                        isExpanded={command.isExpanded}
                        net={command.showNetPrices}
                        configurables={command.configurables}
                    />
                </IfCollectionNotEmpty>
            );
        }

        return(
            <OraModal open={this.props.open} onClose={this.props.onClose} size="medium">
                <p className="h3">{this.props.descriptions && this.props.descriptions['vasModalTitle'] || 'Dobierz us≈Çugi dodatkowe'}</p>
                <Expander variant="section" className="opl-vas-modal-expander" scrollToSelected={false}>
                    <div className="o-separator o-separator--m"/>
                    {
                        this.state.productGroups
                            .map(({section, products}, index) => {
                                const type = section.productFilter;
                                const isExpanded = this.props.selectedSection === type;
                                const key = "TPG_" + index + this.props.open;
                                return (
                                    <div key={key} className="o-separator o-separator--m">
                                        {
                                            renderSection(
                                                {
                                                    ...this.props,
                                                    onClickWrapped: this._onClickInterceptor.bind(this),
                                                    index:  index,
                                                    section: section,
                                                    products: products,
                                                    isExpanded: isExpanded,
                                                    type: type
                                                }
                                            )
                                        }
                                    </div>
                                );
                            })
                    }
                </Expander>
                {
                    this.state.popup &&
                    <Warning
                        onAccept={this._runInterceptedClick.bind(this)}
                        onCancel={this._cancelInterceptedClick.bind(this)}
                        modalContents={this.state.modalContent}
                    />
                }
                <div className="l-row">
                    <div className="l-col l-col-small-12 l-col-medium-8 l-col-8"/>
                </div>
                <SaveVasesButton
                    saveVasesClicked={this.onSaveWrapper}
                    saveVasesButtonText={this.props.saveVasesButtonText}
                />
            </OraModal>
        )
    }
}